.nav {
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
  margin-bottom: 0;

  &.first-child li:first-child .nav-link {
    padding-left: 0;
    padding-right: 0;
    margin-right: 1rem;
  }
}

.nav-item {
  border: 1px solid $border-color;
}

.nav-link {
  @include media-breakpoint-up(md) {
    padding: .5625rem 2rem;
    font-size: 1.25rem;
  }

}

.nav-underline:hover {
  box-shadow: inset 0 -10px 0 0 #aa0601;
}